	.checked {
    color: orange;
	}
	
	html, body
	{
		height: 100%;
	}
	
	body
	{
		margin: 0px;
		padding: 0px;
		background: #202020;
		font-family: Palatino, 'Palatino Linotype', 'Palatino LT STD', 'Book Antiqua', Georgia, serif;
		font-size: 12pt;
		font-weight: 300;
		color: #444444;
	}
	
	
	h1, h2, h3
	{
		margin: 0;
		padding: 0;
		font-weight: 600;
		color: #333333;
	}
	
	p, ol, ul
	{
		margin-top: 0;
	}
	@media only screen and (max-width: 400px) {
		ol, ul
		{
			padding: 0em 6em;
		}
	
	}
	ol, ul
	{
		padding: 0;
		list-style: none;
	}
	
	p
	{
		line-height: 180%;
	}
	

	a
	{
		color: #2980b9;
	}
	
	a:hover
	{
		text-decoration: none;
	}
	
	.container
	{
		overflow: hidden;
		margin: 0em auto;
		width: 1200px;
	}
	


/*********************************************************************************/
/* Heading Titles                                                                */
/*********************************************************************************/

	.title
	{
		margin-bottom: 3em;
	}
	
	.title h2
	{

		font-size: 2.8em;
		font-style: oblique;
	}
	
	.title .byline
	{
		font-size: 1.3em;
		color: #6F6F6F;
	}
		
/*********************************************************************************/
/* Header                                                                        */
/*********************************************************************************/

	#header
	{
		position: relative;
		float: left;
		width: 20%;
		height: 100%;
		overflow: hidden;
		padding: 3em 2em;
	}
	@media only screen and (max-width: 400px) {
		#header
		{
			position: relative;
			float: left;
			width: 100%;
			padding: 3em 0em;
		}
	
	}
	#header ul 
	{
		list-style-type: circle;
		font-weight: 700;
		color: rgba(255,255,255,0.5);
		position: absolute;
	}

	#header ul li 
	{
		padding: 15px 0px;
		font-size: 1em;
	}

/*********************************************************************************/
/* Logo                                                                          */
/*********************************************************************************/

	#logo
	{
		text-align: center;
		margin-bottom: 0em;
	}
	
	#logo h1
	{
		display: block;
	}
	
	#logo a
	{
		text-decoration: none;
		color: #FFF;
	}
	
	#logo span
	{
		padding-right: 0.1em;
		text-transform: uppercase;
		font-size: 0.90em;
		color: rgba(255,255,255,0.3);
	}

	#logo span a 
	{
		color: rgba(255,255,255,0.5);
	}
	
	#logo img
	{
		display: inline-block;
		margin-bottom: 1em;
		border-radius: 50%;
		position: relative;
		width: 100%;
		height: 100%;
	}

	#logo2 img
	{
		width: 100px;
		height: 100px;
	}

/*********************************************************************************/
/* navigation                                                                          */
/*********************************************************************************/
	#navigation 
	{
		margin: 0px;
		padding: 0px;
   		height: 40px;
   		width: 1138px;
    	background: #202020;
		position: relative;
		left: 20%;
	}

	#navigation h1 
	{
		color: #2980b9;
		display:block;
	}

	#navigation li 
	{
		float: left;
	}

	#navigation ul 
	{
		list-style-type: none;
    	margin: 0;
    	padding: 0;
    	overflow: hidden;
	}

	li a 
	{
    	display: block;
		padding: 1em 5em;
		text-align: center;
		text-decoration:  none;
		text-transform: uppercase;
		font-weight: 700;
		color: rgba(255,255,255,0.5);
	}


/*********************************************************************************/
/* Page                                                                          */
/*********************************************************************************/

	#page
	{
		position: absolute;
		background: #2a2a2a;
