Button {
	box-shadow: 0px 10px 14px -7px #276873;
	background:linear-gradient(to bottom, #b35959 5%, #407599 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#b35959', endColorstr='#407599',GradientType=0);
	background-color:#b35959;
	border-radius:8px;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:20px;
	font-weight:bold;
	padding:13px 32px;
	text-decoration:none;
	text-shadow:0px 1px 0px #3d768a;
	position: relative;
	left:45%;
}
Button:hover {
	background:linear-gradient(to bottom, #407599 5%, #b35959 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#407599', endColorstr='#b35959',GradientType=0);
	background-color:#407599;
}
Button:active {
	position:relative;
	top:1px;
}






